import React, { useEffect, useRef } from 'react';
import { Box } from '@mui/material';
import Header from '../components/header';
import { Screen, Section } from '../components/screen';
import Footer from '../components/footer';
import { WorkType, works } from '../lib/dicography-base';
import mediumZoom from 'medium-zoom';
import { blueGrey } from '@mui/material/colors';
import theme from '../lib/theme';

interface ZoomImageProps {
    src: string;
    largeSrc: string;
    alt?: string;
    className?: string;
}

const ZoomImage: React.FC<ZoomImageProps> = ({ src, largeSrc, alt, className }) => {
    const imgRef = useRef<HTMLImageElement | null>(null);

    useEffect(() => {
        let zoom: any | null = null;

        if (imgRef.current) {
            zoom = mediumZoom(imgRef.current, {
                margin: 80,
                background: 'rgba(0, 0, 0, .7)',
                scrollOffset: 0,
            });
        }
        return () => {
            if (zoom) {
                zoom.detach();
            }
        };
    }, [src]);

    return <img src={src} style={{ width: '100%' }} data-zoom-src={largeSrc} alt={alt} className={className} ref={imgRef} />;
};

const Work: React.FC<{ work: WorkType }> = ({ work }) => {
    return (
        <Box sx={{
            width: 192,
            height: 256,
        }}>
            <ZoomImage src={work.thumbUrl} largeSrc={work.imageUrl} alt="Description of Image" />
            <Box sx={{
                width: '100%',
                my: 1,
                color: blueGrey[300],
                fontWeight: 700,
                fontSize: 20,
                textAlign: 'center',
                [theme.breakpoints.up('md')]: {
                    textAlign: 'left',
                },
            }}>{work.title}</Box>
            <Box sx={{
                width: '100%',
                textAlign: 'center',
                [theme.breakpoints.up('md')]: {
                    textAlign: 'left',
                },
            }}>{work.releaseAt}</Box>
        </Box >
    );
}

const Discography: React.FC = () => {
    return (
        <Screen>
            <Header title={"Discography"} />
            <Section>
                <Box sx={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    rowGap: 8,
                    justifyContent: 'center',
                    columnGap: 4,
                    [theme.breakpoints.up('md')]: {
                        rowGap: 12,
                        justifyContent: 'flex-start',
                    },
                }}>
                    {works.map(work => (
                        <Work work={work} key={work.imageUrl} />
                    ))}
                </Box>
            </Section>
            <Footer />
        </Screen>
    );
}

export default Discography;
