import { styled } from '@mui/material/styles';
import { ReactNode, useState } from 'react';
import nebula from './nebula_dark.jpg';
import { Box } from '@mui/material';
import { blueGrey } from '@mui/material/colors';
import { Link as RouterLink } from 'react-router-dom';
import theme from '../lib/theme';

export const Link: React.FC<{ children: ReactNode, to: string, props?: any }> = ({ children, to, ...props }) => {
    const [isHover, setIsHover] = useState(false);

    const handleMouseEnter = () => {
        setIsHover(true);
    }

    const handleMouseLeave = () => {
        setIsHover(false);
    }

    return (
        <RouterLink
            style={{
                textDecoration: isHover ? 'underline' : 'none',
                color: isHover ? blueGrey[300] : blueGrey[500],
                fontWeight: 600,
                textShadow: '1px 1px 0px #15151560',
            }}
            to={to}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            {...props}
        >
            {children}
        </RouterLink >
    )
}

interface SectionProps {
    disableGutters?: boolean;
}

export const Section = styled('div')<SectionProps>(({ theme, disableGutters }) => ({
    paddingLeft: 16,
    paddingRight: 16,
    paddingBottom: 28,
    [theme.breakpoints.up('md')]: {
        paddingLeft: 20,
        paddingRight: 20,
    },

    /*
    paddingLeft: (disableGutters === true ? 0 : 16),
    paddingRight: (disableGutters === true ? 0 : 16),
    paddingTop: 16,
    paddingBottom: 16,
*/
}));

export const Screen: React.FC<{ children: ReactNode }> = ({ children }) => {

    return (
        <Box sx={{
            minHeight: '100vh',
            background: `url(${nebula}) no-repeat 70% 50% fixed`,
            display: 'flex',
            flexDirection: 'column',
            [theme.breakpoints.up('md')]: {
                background: `url(${nebula}) no-repeat center fixed`,
                px: 4,
                py: 4,
            },
        }}>
            <Box sx={{
                py: 4,
                px: 0,
                [theme.breakpoints.up('md')]: {
                    px: 8,
                    py: 8,
                },
                flex: 1,
                display: 'flex',
                flexDirection: 'column',
            }}>
                <Box sx={{
                    borderLeft: `1px solid ${blueGrey[600]}`,
                    px: 1,
                    py: 2,
                    flex: 1,
                    display: 'flex',
                    flexDirection: 'column',
                    [theme.breakpoints.up('md')]: {
                        px: 4,
                        py: 4,
                    },
                }}>
                    {children}
                </Box>
            </Box>
        </Box >
    );
}