import React, { useState } from 'react';
import { Box, List, ListItem, ListItemButton, ListItemText } from '@mui/material';
import Header from '../components/header';
import { Screen, Section } from '../components/screen';
import Footer from '../components/footer';
import AudioPlayer from 'react-h5-audio-player';
import './audio-player.scss';
import { blueGrey } from '@mui/material/colors';
import { Audio, AudioBase } from '../lib/audio-base';

const AudioWorks: React.FC = () => {
    const [curAudio, setCurAudio] = useState<Audio | null>(null);

    const handleClickPrevious = (): void => {
        setCurAudio((prevCurAudio: Audio | null) => {
            const idx = AudioBase.findIndex(audio => audio.file === prevCurAudio?.file);
            return AudioBase[idx > 0? idx - 1: AudioBase.length - 1];
        })
    }

    const handleClickNext = (): void => {
        setCurAudio((prevCurAudio: Audio | null) => {
            const idx = AudioBase.findIndex(audio => audio.file === prevCurAudio?.file);
            return AudioBase[idx < AudioBase.length - 1? idx + 1: 0];
        })
    }

    const audioSelect = (audio: Audio) => {
        setCurAudio(audio);
        console.log(curAudio);
        setTimeout(() => {
            console.log(curAudio);
        }, 0);
    }

    return (
        <Screen>
            <Header title="Audio Works" />
            <Section sx={{ flex: 1, display: 'flex', flexDirection: 'column' }}>
                <AudioPlayer showSkipControls showJumpControls={false}
                    src={curAudio ? `${process.env.PUBLIC_URL}/mp3/${curAudio.file}` : undefined}
                    onClickPrevious={handleClickPrevious}
                    onClickNext={handleClickNext}
                    onEnded={handleClickNext}
                    showFilledVolume
                    loop={false}
                    customAdditionalControls={[]}
                    style={{
                        border: '1px solid #c4c4c4',
                    }}
                />
                <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column' }}>
                    <List sx={{
                        mt: 2,
                        flex: 1,
                        maxHeight: 320,
                        overflow: 'auto'
                    }}>
                        {AudioBase.map(audio => (
                            <ListItem key={audio.file}>
                                <ListItemButton onClick={() => audioSelect(audio)}
                                    sx={audio.file === curAudio?.file ? { border: 1, borderColor: `${blueGrey[100]}` } : {}}>
                                    <ListItemText>{audio.title} - {audio.genre} {audio.length}</ListItemText>
                                </ListItemButton>
                            </ListItem>
                        ))}
                    </List>
                </Box>
            </Section>
            <Footer />
        </Screen>
    );
}

export default AudioWorks;
